*{
  font-family:  Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.square{
  height: 50px;
  width: 50px;
  border: 1px solid grey;
  /* margin:1px; */
  font-weight: 900;
  background: white;
  font-size: 25px;
  overflow: hidden;
}

.win{
  background: rgba(0, 128, 0, 0.31);
}

.row{
  display: block;
  margin-left: 10px;
}
.row:first-child{
  margin-top: 10px;
}
.row{
  height: 50px;
  overflow: hidden;
  padding: 0;
  display: flex;
  align-items: center;
}